'use strict';

const browser = require('../util/browser');

const gamma = 0.105 / browser.devicePixelRatio;

module.exports = drawSDFIcons;

function drawSDFIcons(painter, sourceCache, layer, coords) {
    if (painter.isOpaquePass) return;

    const gl = painter.gl;

    gl.disable(gl.STENCIL_TEST);

    painter.setDepthSublayer(0);
    painter.depthMask(false);
    drawLayerIcons(painter, sourceCache, layer, coords, false,
        layer.paint['icon-translate'],
        layer.paint['icon-translate-anchor'],
        layer.layout['icon-rotation-alignment'],
        // icon-pitch-alignment is not yet implemented
        // and we simply inherit the rotation alignment
        layer.layout['icon-rotation-alignment'],
        /*
          We use data-driven scaling, which is applied when constructing the
          icon quads.
          layer.layout['sdficon-scale']
        */
        1,
        layer.paint['icon-halo-width'],
        layer.paint['icon-halo-color'],
        layer.paint['icon-halo-blur'],
        layer.paint['icon-opacity'],
        layer.paint['icon-color']
    );
}


function drawLayerIcons(painter, sourceCache, layer, coords, isText, translate,
  translateAnchor, rotationAlignment, pitchAlignment, size, haloWidth,
  haloColor, haloBlur, opacity, color) {

    if (painter.style.sprite && !painter.style.sprite.loaded()) {
        return;
    }

    const gl = painter.gl;

    const rotateWithMap = rotationAlignment === 'map';
    const pitchWithMap = pitchAlignment = 'map';

    const depthOn = pitchWithMap;

    if (depthOn) {
        gl.enable(gl.DEPTH_TEST);
    } else {
        gl.disable(gl.DEPTH_TEST);
    }

    gl.disable(gl.DEPTH_TEST);

    for (const coord of coords) {
        const tile = sourceCache.getTile(coord);
        const bucket = tile.getBucket(layer);
        if (!bucket) { continue; }
        const buffers = bucket.buffers;

        if (!buffers || !buffers.segments.length) continue;

        const layerData = buffers.layerData[layer.id];
        const programConfiguration = layerData.programConfiguration;
        const program = painter.useProgram('sdficon', programConfiguration);

        setIconDrawState(
          program, painter, isText, true /* isSDF */, rotateWithMap, pitchWithMap,
          bucket.fontstack, size, bucket.iconsNeedLinear,
          bucket.adjustedIconSize, opacity
        );

        // painter.enableTileClippingMask(coord); // probably not needed
        gl.uniformMatrix4fv(program.u_matrix, false, coord.posMatrix);
        drawTileIcons(program, painter, layer, tile, buffers, isText, true /* isSDF */,
                pitchWithMap, size, haloWidth, haloColor, haloBlur, color);
    }
}

function setIconDrawState(program, painter, isText, isSDF, rotateWithMap,
  pitchWithMap, fontstack, size, iconsNeedLinear, adjustedSize, opacity) {

    const gl = painter.gl;
    const tr = painter.transform;

    const fontScale = size / (isText ? 24 : 1);

    const s = tr.cameraToCenterDistance * fontScale;
    gl.uniform2f(program.u_extrude_scale, tr.pixelsToGLUnits[0] * s, tr.pixelsToGLUnits[1] * s);

    gl.activeTexture(gl.TEXTURE0);
    gl.uniform1i(program.u_texture, 0);

    const mapMoving = painter.options.rotating || painter.options.zooming;
    const iconScaled = size !== 1 || browser.devicePixelRatio !== painter.spriteAtlas.pixelRatio || iconsNeedLinear;
    const iconTransformed = pitchWithMap || tr.pitch;
    painter.spriteAtlas.bind(gl, isSDF || mapMoving || iconScaled || iconTransformed);
    gl.uniform2f(program.u_texsize, painter.spriteAtlas.width / 4, painter.spriteAtlas.height / 4);

    gl.activeTexture(gl.TEXTURE1);
    painter.frameHistory.bind(gl);
    gl.uniform1i(program.u_fadetexture, 1);

    // adjust min/max zooms for variable font sizes
    const zoomAdjust = Math.log(size / adjustedSize) / Math.LN2 || 0;
    gl.uniform1f(program.u_zoom, (tr.zoom - zoomAdjust) * 10); // current zoom level

    gl.uniform1f(program.u_pitch, tr.pitch / 360 * 2 * Math.PI);
    gl.uniform1f(program.u_bearing, tr.bearing / 360 * 2 * Math.PI);
    gl.uniform1f(program.u_aspect_ratio, tr.width / tr.height);

    gl.uniform1f(program.u_opacity, opacity);
}


function drawTileIcons(program, painter, layer, tile, buffers, isText, isSDF,
        pitchWithMap, size/*, haloWidth, haloColor, haloBlur, color */) {

    const gl = painter.gl;
    const tr = painter.transform;

    const fontScale = size / (isText ? 24 : 1);
    const gammaScale = fontScale * (pitchWithMap ? Math.cos(tr._pitch) : 1) * tr.cameraToCenterDistance;

    gl.uniform1f(program.u_gamma, gamma / gammaScale);
    gl.uniform1f(program.u_buffer, (256 - 64) / 256);
    gl.uniform1f(program.u_opacity, 1);
    // UBILABS: we use data-driven coloring
    // gl.uniform4fv(program.u_color, color);

    drawIconElements(buffers, layer, gl, program);
}

function drawIconElements(buffers, layer, gl, program) {
    for (const segment of buffers.segments) {
        const layerData = buffers.layerData[layer.id];
        segment.vaos[layer.id].bind(
          gl,
          program,
          buffers.layoutVertexBuffer,
          buffers.elementBuffer,
          layerData.paintVertexBuffer,
          segment.vertexOffset
        );
        gl.drawElements(gl.TRIANGLES, segment.primitiveLength * 3, gl.UNSIGNED_SHORT, segment.primitiveOffset * 3 * 2);
    }
}
