'use strict';

module.exports = drawRaster;

function drawRaster(painter, source, layer, coords) {
    if (painter.isOpaquePass) return;

    const gl = painter.gl;

    gl.enable(gl.DEPTH_TEST);
    painter.depthMask(true);

    // Change depth function to prevent double drawing in areas where tiles overlap.
    gl.depthFunc(gl.LESS);

    const minTileZ = coords.length && coords[0].z;

    // setup look up texture
    const imageData = layer.paint['raster-lookup-texture'];
    bindTextureToContext(gl, gl.TEXTURE2, imageData);

    for (let i = 0; i < coords.length; i++) {
        const coord = coords[i];
        // set the lower zoom level to sublayer 0, and higher zoom levels to higher sublayers
        painter.setDepthSublayer(coord.z - minTileZ);
        drawRasterTile(painter, source, layer, coord);
    }

    gl.depthFunc(gl.LEQUAL);
}

function drawRasterTile(painter, sourceCache, layer, coord) {

    const gl = painter.gl;

    gl.disable(gl.STENCIL_TEST);

    const tile = sourceCache.getTile(coord);
    const posMatrix = painter.transform.calculatePosMatrix(coord, sourceCache.getSource().maxzoom);

    tile.registerFadeDuration(painter.style.animationLoop, layer.paint['raster-fade-duration']);

    const program = painter.useProgram('data-driven-raster');
    gl.uniformMatrix4fv(program.u_matrix, false, posMatrix);

    // (ubilabs): not required: color-parameters

    gl.activeTexture(gl.TEXTURE0);
    gl.bindTexture(gl.TEXTURE_2D, tile.texture);

    // (ubilabs) removed: setup parent-tile for TEXTURE1
    // (ubilabs) removed: parameter-uniforms for cross-fading

    gl.uniform1f(program.u_buffer_scale, 1);
    gl.uniform1i(program.u_image0, 0);
    gl.uniform1i(program.u_look_up_texture, 2);

    const buffer = tile.boundsBuffer || painter.rasterBoundsBuffer;
    const vao = tile.boundsVAO || painter.rasterBoundsVAO;
    vao.bind(gl, program, buffer);
    gl.drawArrays(gl.TRIANGLE_STRIP, 0, buffer.length);
}

/**
 * Activates the a given gl texture unit, creates an new texture from given
 *   imageData and binds the texture to the gl context.
 * @param  {WebGlRenderingContext} gl  The gl context
 * @param  {gl.TEXTUREi} textureUnit  The texture unit
 * @param  {ImageData} imageData  An imageData object to create the texture from
 */
function bindTextureToContext(gl, textureUnit, imageData) {
    gl.activeTexture(textureUnit);
    const lookUpTexture = gl.createTexture();
    gl.bindTexture(gl.TEXTURE_2D, lookUpTexture);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, gl.NEAREST);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, gl.NEAREST);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.CLAMP_TO_EDGE);
    gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.CLAMP_TO_EDGE);
    gl.texImage2D(gl.TEXTURE_2D, 0, gl.RGBA, gl.RGBA, gl.UNSIGNED_BYTE, imageData);
    lookUpTexture.size = imageData.width;
}
