'use strict';


const Anchor = require('../../symbol/anchor');
const Bucket = require('../bucket');
const createElementArrayType = require('../element_array_type');
const createVertexArrayType = require('../vertex_array_type');
const loadGeometry = require('../load_geometry');
const Quads = require('../../symbol/quads');
const resolveTokens = require('../../util/token');
const Shaping = require('../../symbol/shaping');
const util = require('../../util/util');
const VectorTileFeature = require('vector-tile').VectorTileFeature;


const EXTENT = require('../extent');

const getSDFIconQuads = Quads.getSDFIconQuads;
const shapeIcon = Shaping.shapeIcon;

const elementArrayType = createElementArrayType();
const layoutVertexArrayType = createVertexArrayType([
    {name: 'a_pos',         components: 2, type: 'Int16'},
    {name: 'a_offset',      components: 2, type: 'Int16'},
    {name: 'a_texture_pos', components: 2, type: 'Uint16'},
    {name: 'a_data',        components: 4, type: 'Uint8'}
]);

const sdfIconInterface = {
    layoutVertexArrayType: layoutVertexArrayType,
    elementArrayType: elementArrayType,
    // UBILABS: Data-driven icon color
    paintAttributes: [
        {
            property: 'sdficon-color',
            type: 'Uint8'
        }
    ]
};


function addVertex(array, x, y, ox, oy, tx, ty, minzoom, maxzoom) {
    array.emplaceBack(
            // a_pos
            x,
            y,

            // a_offset
            Math.round(ox * 64),
            Math.round(oy * 64),

            // a_texture_pos
            tx / 4, // x coordinate of symbol on glyph atlas texture
            ty / 4, // y coordinate of symbol on glyph atlas texture

            // a_data
            0, // labelminzoom
            0, // labelangle
            (minzoom || 0) * 10, // minzoom
            Math.min(maxzoom || 25, 25) * 10); // maxzoom
}

class SDFIconBucket extends Bucket {
    constructor(options) {
        super(options, sdfIconInterface);

        this.collisionBoxArray = options.collisionBoxArray;
        this.symbolQuadsArray = options.symbolQuadsArray;
        this.symbolInstancesArray = options.symbolInstancesArray;

        this.zoom = options.zoom;
        this.overscaling = options.overscaling;
        this.layers = options.layers;
        this.index = options.index;
        this.sdfIcons = options.sdfIcons;
        this.iconsNeedLinear = options.iconsNeedLinear;
        this.adjustedTextSize = options.adjustedTextSize;
        this.adjustedIconSize = options.adjustedIconSize;
        this.fontstack = options.fontstack;
    }

    populate(features, options) {
        const layout = this.layers[0].layout;
        const iconImage = layout['sdficon-image'];
        const hasIcon = iconImage;
        this.features = [];

        if (!hasIcon) {
            return;
        }

        const icons = options.iconDependencies;

        for (let i = 0; i < features.length; i++) {
            const feature = features[i];
            if (!this.layers[0].filter(feature)) {
                continue;
            }

            let icon;
            if (hasIcon) {
                icon = resolveTokens(feature.properties, iconImage);
            }

            if (!icon) {
                continue;
            }

            this.features.push({
                icon,
                index: i,
                sourceLayerIndex: feature.sourceLayerIndex,
                geometry: loadGeometry(feature),
                properties: feature.properties,
                type: VectorTileFeature.types[feature.type]
            });

            if (icon) {
                icons[icon] = true;
            }
        }
    }

    isEmpty() {
        return this.arrays.isEmpty();
    }

    serialize(transferables) {
        return {
            zoom: this.zoom,
            layerIds: this.layers.map((l) => l.id),
            sdfIcons: this.sdfIcons,
            iconsNeedLinear: this.iconsNeedLinear,
            adjustedTextSize: this.adjustedTextSize,
            adjustedIconSize: this.adjustedIconSize,
            fontstack: this.fontstack,
            arrays: this.arrays.serialize(transferables)
        };
    }

    destroy() {
        if (this.buffers) {
            if (this.buffers.icon) this.buffers.icon.destroy();
            if (this.buffers.glyph) this.buffers.glyph.destroy();
            if (this.buffers.collisionBox) this.buffers.collisionBox.destroy();
            this.buffers = null;
        }
    }

    // prepare: this.features --> this.symbolInstancesArray

    prepare(stacks, icons) {
        const tileSize = 512 * this.overscaling;
        this.tilePixelRatio = EXTENT / tileSize;
        this.iconsNeedLinear = false;
        this.symbolInstancesStartIndex = this.symbolInstancesArray.length;

        const layout = this.layers[0].layout;

        for (const feature of this.features) {
            let shapedIcon;
            if (feature.icon) {
                const image = icons[feature.icon];
                shapedIcon = shapeIcon(image, [0, 0]); // fixed icon-offset

                if (image) {
                    if (this.sdfIcons === undefined) {
                        this.sdfIcons = image.sdf;
                    } else if (this.sdfIcons !== image.sdf) {
                        util.warnOnce('Style sheet warning: Cannot mix SDF and non-SDF icons in one buffer');
                    }
                    if (image.pixelRatio !== 1) {
                        this.iconsNeedLinear = true;
                    } else if (layout['sdficon-rotate'] !== 0 || !this.layers[0].isLayoutValueFeatureConstant('icon-rotate')) {
                        this.iconsNeedLinear = true;
                    }
                }
            }

            if (shapedIcon) {
                this.addFeature(feature, shapedIcon);
            }
        }
        this.symbolInstancesEndIndex = this.symbolInstancesArray.length;
    }

    addFeature(feature, shapedIcon) {
        const iconBoxScale = this.tilePixelRatio * this.adjustedIconSize;

        const addSymbolInstance = (line, anchor) => {
            const inside = !(anchor.x < 0 || anchor.x > EXTENT || anchor.y < 0 || anchor.y > EXTENT);

            if (/* avoidEdges && */ !inside) return;

            const addToBuffers = inside/* || mayOverlap */;
            this.addSymbolInstance(
              anchor,
              line,
              null /* shapedTextOrientations */,
              shapedIcon,
              this.layers[0],
              addToBuffers,
              this.symbolInstancesArray.length,
              this.collisionBoxArray,
              feature.index,
              feature.sourceLayerIndex,
              this.index,
              null /* textBoxScale */,
              null /* textPadding */,
              null /* textAlongLine */,
              iconBoxScale,
              null /* iconPadding */,
              null /* iconAlongLine */,
              {zoom: this.zoom},
              feature.properties
            );
        };

        if (feature.type === 'Point') {
            for (const points of feature.geometry) {
                for (const point of points) {
                    addSymbolInstance([point], new Anchor(point.x, point.y, 0));
                }
            }
        }
    }

    // place: this.symolInstancesArray --> layoutVertexArray

    place() {
        for (let p = this.symbolInstancesStartIndex; p < this.symbolInstancesEndIndex; p++) {
            const symbolInstance = this.symbolInstancesArray.get(p);
            this.addSymbols(
              this.arrays,
              symbolInstance.iconQuadStartIndex,
              symbolInstance.iconQuadEndIndex,
              null /* iconScale */, // comes from collision stuff, is mostly 0.5?
              null /* layout['icon-keep-upright'] */,
              null /* iconAlongLine */,
              1 /* collisionTile.angle */
            );

            // we used the textBoxStartIndex to store the feature index in
            // this.addSymbolInstance
            const featureIndex = symbolInstance.textBoxStartIndex;
            const feature = this.features[featureIndex];
            this.arrays.populatePaintArrays(feature.properties);
        }
    }

    addSymbols(arrays, quadsStart, quadsEnd, scale/*, keepUpright, alongLine,
      placementAngle , writingModes */) {
        const elementArray = arrays.elementArray;
        const layoutVertexArray = arrays.layoutVertexArray;
        const zoom = this.zoom;
        const placementZoom = Math.max(Math.log(scale) / Math.LN2 + zoom, 0);
        for (let k = quadsStart; k < quadsEnd; k++) {

            const symbol = this.symbolQuadsArray.get(k).SymbolQuad;
            const tl = symbol.tl,
                tr = symbol.tr,
                bl = symbol.bl,
                br = symbol.br,
                tex = symbol.tex,
                anchorPoint = symbol.anchorPoint;

            const minZoom = 1;
            const maxZoom = 25;
            // Encode angle of glyph
            const glyphAngle = Math.round((symbol.glyphAngle / (Math.PI * 2)) * 256);

            const segment = arrays.prepareSegment(4);
            const index = segment.vertexLength;
            addVertex(layoutVertexArray, anchorPoint.x, anchorPoint.y, tl.x, tl.y, tex.x, tex.y, minZoom, maxZoom, placementZoom, glyphAngle);
            addVertex(layoutVertexArray, anchorPoint.x, anchorPoint.y, tr.x, tr.y, tex.x + tex.w, tex.y, minZoom, maxZoom, placementZoom, glyphAngle);
            addVertex(layoutVertexArray, anchorPoint.x, anchorPoint.y, bl.x, bl.y, tex.x, tex.y + tex.h, minZoom, maxZoom, placementZoom, glyphAngle);
            addVertex(layoutVertexArray, anchorPoint.x, anchorPoint.y, br.x, br.y, tex.x + tex.w, tex.y + tex.h, minZoom, maxZoom, placementZoom, glyphAngle);

            elementArray.emplaceBack(index, index + 1, index + 2);
            elementArray.emplaceBack(index + 1, index + 2, index + 3);

            segment.vertexLength += 4;
            segment.primitiveLength += 2;
        }
    }

    addSymbolInstance(anchor, line, shapedTextOrientations, shapedIcon, layer,
      addToBuffers, index, collisionBoxArray, featureIndex, sourceLayerIndex,
      bucketIndex, textBoxScale, textPadding, textAlongLine, iconBoxScale,
      iconPadding, iconAlongLine, globalProperties, featureProperties) {
        let iconQuads;
        const glyphQuadIndex = this.symbolQuadsArray.length;

        if (shapedIcon) {
            iconQuads = addToBuffers ? getSDFIconQuads(
              anchor,
              shapedIcon,
              iconBoxScale,
              line,
              layer,
              false /* iconAlongLine */,
              null /* shapedTextOrientations[WritingMode.horizontal] */,
              globalProperties,
              featureProperties
            ) : [];
        }

        const iconQuadStartIndex = this.symbolQuadsArray.length;
        if (iconQuads && iconQuads.length === 1) {
            this.addSymbolQuad(iconQuads[0]);
        }
        const iconQuadEndIndex = this.symbolQuadsArray.length;

        const iconBoxIndex = this.collisionBoxArray.length;

        if (iconQuadEndIndex > SDFIconBucket.MAX_QUADS) {
            util.warnOnce("Too many symbols being rendered in a tile. See https://github.com/mapbox/mapbox-gl-js/issues/2907");
        }

        return this.symbolInstancesArray.emplaceBack(
            featureIndex, // actually `textBoxStartIndex` but reused as feature index
            0, // textBoxEndIndex,
            iconBoxIndex, // iconBoxStartIndex,
            iconBoxIndex, // iconBoxEndIndex,
            glyphQuadIndex, // glyphQuadStartIndex,
            glyphQuadIndex, // glyphQuadEndIndex,
            iconQuadStartIndex,
            iconQuadEndIndex,
            anchor.x,
            anchor.y,
            index,
            0 // writingModes
        );
    }

    addSymbolQuad(symbolQuad) {
        return this.symbolQuadsArray.emplaceBack(
            // anchorPoints
            symbolQuad.anchorPoint.x,
            symbolQuad.anchorPoint.y,
            // corners
            symbolQuad.tl.x,
            symbolQuad.tl.y,
            symbolQuad.tr.x,
            symbolQuad.tr.y,
            symbolQuad.bl.x,
            symbolQuad.bl.y,
            symbolQuad.br.x,
            symbolQuad.br.y,
            // texture
            symbolQuad.tex.h,
            symbolQuad.tex.w,
            symbolQuad.tex.x,
            symbolQuad.tex.y,
            //angle
            symbolQuad.anchorAngle,
            symbolQuad.glyphAngle,
            // scales
            symbolQuad.maxScale,
            symbolQuad.minScale,
            // writing mode
            symbolQuad.writingMode);
    }
}
SDFIconBucket.MAX_QUADS = 65535;

module.exports = SDFIconBucket;
